/* CSS FOR ALL PAGES */

/* Styling the Nav Bar */
.navbar {
    background-color: #343a40;
    position: fixed !important;
    width: 100%;
    top: 0;
}

.navbar-brand, .navbar-brand:hover {
    color: #61dafb;
}

.navbar .nav-item a.nav-link {
    color: lightgray;
  }

  .navbar .nav-item a.nav-link:hover {
    color: white;
    cursor: pointer;
  }

  .navbar .nav-item.active a.nav-link  {
    color: white;
  }

  /* Styling the main container that holds pages */
  .content {
    padding-top: 80px;  
    padding-left: 50px;
    padding-right: 50px;
  }

  /* Specifications for all quotes, code, images, and tables */
  pre {
    border: 1px solid #ccc;
  }
  
  blockquote {
    color: #666;
    margin: 0;
    padding-left: 3em;
    border-left: 0.5em #eee solid;
  }
  
  tr {
    border-top: 1px solid #c6cbd1;
    background: #fff;
  }
  
  th,
  td {
    padding: 6px 13px;
    border: 1px solid #dfe2e5;
  }
  
  table tr:nth-child(2n) {
    background: #f6f8fa;
  }

  /* STYLING FOR THE COMPARING IMPUTATION METHODS TUTORIAL */
  .comparing-imputation-methods {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    float: left;
  }

  .cim-code-1 {
    width: 50%;
    padding-right: 5px;
    float: left;
  }
  
  .cim-table-code-1, .cim-df-scatter-01 {
    width: 50%;
    float: left;
    height: 50%;
  }

  .cim-df-scatter-01 {
    padding-top: 5px;
  }

  .dataframe {
    width: 100%;
    float: left;
  }

  img {
    max-width: 95%;
    max-height: 95%;
    padding-bottom: 5px;
 }

/* STYLING FOR THE END TO END TUTORIAL */
.end-to-end {
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
  float: left;
}

.ete-mcar {
  width: 50%;
  padding-right: 10px;
  float: left;
}

.ete-mar {
  width: 50%;
  float: left;
}

.ete-mcar-percent, .ete-mar-percent,
.ete-mcar-locations, .ete-mar-locations,
.ete-mcar-mean-dist, .ete-mar-mean-dist,
.ete-mcar-mean-box, .ete-mar-mean-box {
  height: 400px;
}
